import { Component, OnInit, Input, ViewChild, ElementRef, ChangeDetectorRef, OnChanges } from "@angular/core";
import { NormalService } from 'src/app/normal.service';
import { MatTableDataSource, MatPaginator, PageEvent } from '@angular/material';

interface HistoryElement {
    Id: number
    Type: number,
    Issue: number,
    Data: string,
    Time: string,
}

@Component({
    selector: 'records',
    templateUrl: './records.template.html',
    styleUrls: ['./records.style.scss']
})
export class RecordsComponent implements OnInit, OnChanges {
    @Input() gameId;
    @Input() gameName;
    @Input() playeds;
    displayedColumns = ['GameName', 'GamePeriod', 'betCode', 'WinAmount'];
    dataSource = [];
    openDisplayedColumns = ['GameName', 'GamePeriod', 'Code', 'Ctime'];
    openHistories = new MatTableDataSource<HistoryElement>();
    tabs: boolean[] = [true, false];
    dataSynced: boolean = false;

    pageIndexRecords: number = 0;
    lengthRecords: number = 0;
    pageSizeRecords: number = 10;
    previousPageIndexRecords: number = 0;
    pageIndexOpendata: number = 0;
    lengthOpendata: number = 0;
    pageSizeOpendata: number = 10;
    previousPageIndexOpendata: number = 0;
    // MatPaginator Output
    pageEvent: PageEvent;
    constructor(
        public normalService: NormalService,
        public element: ElementRef,
        private changeDetectorRefs: ChangeDetectorRef,
    ){
    }

    ngOnInit() {
        this.normalService.OpenData$.subscribe(opendata=> {
            if (opendata.type == this.gameId) {
                let data:HistoryElement = {
                    Id: 0,
                    Type: opendata.type,
                    Issue: opendata.last_period,
                    Time: new Date().toISOString(),
                    Data: opendata.last_open,
                }
                this.openHistories.data.unshift(data);
                this.openHistories.data = this.openHistories.data;
            }
        })

        // this.normalService.betCanceled$.subscribe(canceled=>{
        //     this.dataSynced = false;
        //     this.getDataRecords(this.pageIndexRecords);
        // })
        // this.normalService.getLottor$.subscribe(lottor=>{
        //     this.dataSynced = false;
        //     this.getDataRecords(this.pageIndexRecords);
        // })
    }

    ngOnChanges() {
        if (!this.gameId) return;
        this.getDataRecords(this.pageIndexRecords);
    }

    switchTab(id) {
        for (let index = 0; index < this.tabs.length; index++) {
           this.tabs[index] = false
        }
        this.tabs[id] = true;
        if (id == 1) { //查看开奖记录
            this.getDataOpendata(this.pageIndexOpendata);
        }
    }

    getDataOpendata(page) {
        this.normalService.getOpenHistories(this.gameId, this.pageSizeOpendata, this.pageIndexOpendata)
            .subscribe(openHistories => {
                this.openHistories.data = openHistories.data.Records;
                this.dataSynced = true;
                this.lengthOpendata = openHistories.data.Total;
            })
    }

    getDataRecords(page) {
        this.normalService.getHistoryBets({
            gid: Number(this.gameId),
            orderType: 0,
            pageIndex: this.pageIndexRecords,
            pageLimit: this.pageSizeRecords
        })
            .subscribe(histories => {
                this.dataSource = histories.data.Records;
                this.dataSynced = true;
                this.lengthRecords = histories.data.Total;
            });
    }

    pageChangeRecords(pageEvent) {
        this.pageIndexRecords = pageEvent.pageIndex;
        this.pageSizeRecords = pageEvent.pageSize;
        this.previousPageIndexRecords = pageEvent.previousPageIndex;
        this.getDataRecords(pageEvent.pageIndex);
    }

    pageChangeOpendata(pageEvent) {
        this.pageIndexOpendata = pageEvent.pageIndex;
        this.pageSizeOpendata = pageEvent.pageSize;
        this.previousPageIndexOpendata = pageEvent.previousPageIndex;
        this.getDataOpendata(pageEvent.pageIndex);
    }

    freshTable() {
        this.dataSynced = false;
        let id;
        this.tabs.forEach((el, i)=>{
            if (el) id = i;
        })
        if (id == 1) { //查看开奖记录
            this.getDataOpendata(this.pageIndexOpendata);
        } else {
            this.getDataRecords(this.pageIndexRecords);
        }
    }
}