import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from "@angular/core";
import { NormalService } from 'src/app/normal.service';

declare const Swiper;

@Component({
    selector: 'playeds',
    styleUrls: ["./playeds.style.scss"],
    templateUrl: './playeds.template.html',
})
export class PlayedsComponent implements OnInit, OnChanges {
    @Input() gameId;
    @Input() groupType;
    @Input() playeds;
    @Output() subIdChanged = new EventEmitter<number>();
    public subs: any[];
    public selectedIndex = 0;
    public selectedButton;
    public selectedSub: any;
    public tempGameId: number;
    public playedsReading: boolean = true;
    constructor(
        private normalService: NormalService
    ){
      
    }
    ngOnInit() {
        // console.log("playeds component:", this.playeds);
        this.tempGameId = this.gameId;
        this.getSubs();
    }

    ngOnChanges() {
      if (this.gameId != this.tempGameId) {
        this.getSubs();
      }
    }

    getSubs() {
      this.playedsReading = true;
        if (!this.playeds) return;
        if (!this.groupType) {
          this.groupType = this.playeds[0].groupId;
        }
        this.normalService.getSubInfo(String(this.groupType)).subscribe(subs=> {
          this.subs = subs.data.Records;
        //   console.log("subs", this.subs);
            // 为选项赋予赔率
          this.normalService.selectedSubId = this.selectedButton;
          this.normalService.selectedSubName = this.subs[0].SubName;
          this.normalService.selectedPlayedId = this.groupType;
            this.normalService.setBonusProp(this.subs);
          this.selectedButton = this.subs[0].SubId;
          this.selectedSub = this.subs[0];
          this.subIdChanged.emit(this.selectedButton);
          this.playedsReading = false;
        })
    }

    selectedIndexChange(selectedIndex) {
        this.groupType = this.playeds[selectedIndex].groupId;
        this.getSubs();
    }

    changeSubId(sub, button) {
        this.selectedButton = sub.SubId;
        this.selectedSub = sub;
        this.normalService.selectedSubId = sub.SubId;
        this.normalService.selectedSubName = sub.SubName;
        this.normalService.temp_bet_list.betPrize = sub.BetPrize;
        this.normalService.temp_bet_list.betPrizeShow = sub.BetPrize;
        this.subIdChanged.emit(sub.SubId);
    }
}