import { Component, OnInit, Input, OnChanges } from "@angular/core";
import { NormalService } from 'src/app/normal.service';
import { element } from '@angular/core/src/render3';
import { MatTableDataSource, MatSnackBar, MatDialog } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import { ConfirmModalDialog } from '../confirmModal/confirmModal.component';
import { TimeoutModal } from '../timeoutModal/timeoutModal.component';
import { Observable } from 'rxjs';
import { Breakpoints, BreakpointObserver } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';

declare const Swiper;

let ELEMENT_DATA = []

@Component({
    selector: 'add-to-cart',
    styleUrls: ["./addToCart.style.scss"],
    templateUrl: './addToCart.template.html',
})
export class AddToCartComponent implements OnInit, OnChanges {
    @Input() gameId: number;
    @Input() gameName: string;
    @Input() subId: number;
    private pGameId: number;
    public singelAmount: number = 2;
    public priceUnit: number = 1;
    public percent: number = 0;
    public appended: boolean = false;
    public betPrize: number;
    public betPrizeShow: number;
    public tempBetPrizeArr: any[];
    public totalCount: number = 0;
    public moreNum = "5";
    public moreActualNum = 5;
    public betWinStop = true;
    public betStartTime = 1;
    public perPeriodNum = 1;
    public betHigher = false;
    public betHigherTime = 1;
    public maxAppendValue = 50;

    displayedColumns: string[] = ['select', 'period', 'multiple', 'amount', 'openTime'];
    dataSource = new MatTableDataSource<any>(ELEMENT_DATA);
    selection = new SelectionModel<any>(true, []);

    timeoutDialog: any;

    isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );
    constructor(
        public normalService: NormalService,
        public snackBar: MatSnackBar,
        public dialog: MatDialog,
        private breakpointObserver: BreakpointObserver,
    ){
        
    }
    ngOnInit() {
        this.normalService.selectedChanged$.subscribe(sub=>{
            // 计算选中注数
            this.normalService.selectedSubId = sub.subId;
            let betCode: string;
            if (this.normalService.subPlayOptions[sub.subId].input == true) {
                this.normalService.getBetListForInput(sub.subId);
            } else {
                betCode = this.normalService.getBetList(sub.subId);
                // if (this.subId == 31) { //pk10 两面 龙虎
                //     betCode = betCode.replace(/\|/g,"&")
                // }
            }
            this.tempBetPrizeArr = this.normalService.temp_bet_list.betDbHighPrize.split("|")
            this.tempBetPrizeArr.forEach( (element,i)=>{
                this.tempBetPrizeArr[i] = Number(element);
            });
        });
        this.normalService.OpenData$.subscribe(opendata=> {
            if (opendata.type == this.gameId && this.normalService.post_bet.bet_list && this.normalService.post_bet.bet_list.length>0) {
                if (this.timeoutDialog) return;
                this.timeoutDialog = this.dialog.open(TimeoutModal, {
                    width:"400px",
                    data: {title: "期号更新", text: `当前期号已更新，请确认是否要清空您的已选投注单？`}
                })
                this.timeoutDialog.afterClosed().subscribe(result => {
                    if (result && result.result == true) {
                        this.clearCart();
                    }
                    this.timeoutDialog = null;
                });
            }
        })
    }

    ngOnChanges() {
        // 初始化选项配置
        if (this.subId){
            this.normalService.initPlayOptions(this.subId);
        }
        if (this.gameId) {
            if (this.pGameId !== this.gameId) {
                this.normalService.post_bet.bet_list = [];
                this.normalService.post_bet.amount = 0;
                this.totalCount = 0;
                this.appended = false;
                this.moreActualNum = 5;
                this.betWinStop = true;
                this.betHigherTime = 1;
                this.perPeriodNum = 1;
            }
            this.pGameId = this.gameId;
        }
    }
    
    // 修改价格单位
    changeUnit(value) {
        this.priceUnit = value;
    }

    // 改变是否追投
    makeAppend(e) {
        this.appended = e.checked;
        this.initAppendList();
    }

    // 从购物车中删除指定注单
    removeFromCart(index) {
        this.normalService.post_bet.amount -= this.normalService.post_bet.bet_list[index].betMoney;
        this.totalCount -= this.normalService.post_bet.bet_list[index].betCount;
        this.normalService.post_bet.bet_list.splice(index,1);
    }

    // 晴空购物车中的投注单
    clearCart() {
        this.normalService.post_bet.bet_list = [];
        this.normalService.post_bet.amount = 0;
        this.totalCount = 0;
        this.initAppendList();
    }

    // 加入购物车
    addToCart() {
        // 如果是单式，则检查输入的号码
        if (this.normalService.subPlayOptions[this.normalService.selectedSubId].input) {
            let inputArr = this.normalService.handinput.split(/,| |\n|\t/);
            let count = inputArr.length;
            for (let ii = 0; ii < inputArr.length; ii++) {
                const el = inputArr[ii];
                let elArr = [];
                let step = 1;
                for (let index = 0; index < el.length; index+=step) {
                    elArr.push( el.substring(index,index+step) )
                }
                let rightLenth = 5;
                if ([108].indexOf(this.subId) != -1) {
                    rightLenth = 5;
                }
                if ([106,140].indexOf(this.subId) != -1) {
                    rightLenth = 4;
                }
                if ([89,95,96,55,61,62,129,132,134].indexOf(this.subId) != -1) {
                    rightLenth = 3;
                }
                if ([39,47,123,126].indexOf(this.subId) != -1) {
                    rightLenth = 2;
                }
                if (elArr.length!==rightLenth) {
                    this.snackBar.open(`所输入的号码有误`,"确定", {
                        duration:2000
                    });
                    return
                }
                let hasDouble: boolean;
                for (let ii = 0; ii < elArr.length-1; ii++) {
                    for (let iii = ii+1; iii < elArr.length; iii++) {
                        if (elArr[ii] == elArr[iii]) {
                            hasDouble = true;
                        }
                    }
                }
                if ([95,61,132].indexOf(this.subId) != -1) { // 组三 /必须有对
                    if (!hasDouble) {
                        this.snackBar.open(`所输入的号码有误`,"确定", {
                            duration:2000
                        });
                        return
                    }
                    if (elArr[0] == elArr[1] && elArr[1] == elArr[2]) { //豹子号
                        this.snackBar.open(`所输入的号码有误`,"确定", {
                            duration:2000
                        });
                        return
                    }
                }
                if ([96,62,47,126,134].indexOf(this.subId) != -1) { // 任二组选或组六 /不能有对
                    if (hasDouble) {
                        this.snackBar.open(`所输入的号码有误`,"确定", {
                            duration:2000
                        });
                        return
                    }
                }
                // 还需要判断组三和组六的选择
                
                // inputArr[ii] = elArr.join(" ");
            }
            this.normalService.temp_bet_list.betCode = inputArr.join(",")
            if (this.normalService.fangAnNum>1) {
                count*=this.normalService.fangAnNum;
            }
            this.normalService.temp_bet_list.betCount = count;
            this.normalService.temp_bet_list.betEachMoney = this.singelAmount*this.priceUnit;
            this.normalService.temp_bet_list.betMoney = this.normalService.temp_bet_list.betEachMoney*this.normalService.temp_bet_list.betCount;
            this.normalService.temp_bet_list.betReward = this.percent/100;
            this.normalService.temp_bet_list.betPrize = "";
            this.tempBetPrizeArr.forEach((el, i)=>{
                let tempPrize = ((100-this.percent)/100 * el).toFixed(3)
                this.normalService.temp_bet_list.betPrize+=String( tempPrize );
                if (i<this.tempBetPrizeArr.length-1){
                    this.normalService.temp_bet_list.betPrize+="|";
                }
            });
            this.normalService.temp_bet_list.betPrizeShow = this.normalService.temp_bet_list.betPrize;
        } else {
            if (this.normalService.temp_bet_list.betCount == 0) {
                return this.snackBar.open("当前所选号码有误，请核对玩法规则！","确定", {
                    duration: 2000,
                  });
            }
            // 遍历所有选项，并添加至购物车
            this.normalService.temp_bet_list.betEachMoney = this.singelAmount*this.priceUnit;
            this.normalService.temp_bet_list.betCount = this.normalService.selectedCountNum;
            this.normalService.temp_bet_list.betMoney = this.normalService.temp_bet_list.betEachMoney*this.normalService.temp_bet_list.betCount;
            this.normalService.temp_bet_list.betReward = this.percent/100;
            // 组织和返点一致的赔率
            this.normalService.temp_bet_list.betPrize = "";
            this.tempBetPrizeArr.forEach((el, i)=>{
                let tempPrize = ((100-this.percent)/100 * el).toFixed(3)
                this.normalService.temp_bet_list.betPrize+=String( tempPrize );
                if (i<this.tempBetPrizeArr.length-1){
                    this.normalService.temp_bet_list.betPrize+="|";
                }
            });
            this.normalService.temp_bet_list.betPrizeShow = this.normalService.temp_bet_list.betPrize;
        }

        // 组织betPos
        let betPos = "";
        if (this.normalService.posArr.length>0) {
            for (let i = 0; i < this.normalService.posArr.length; i++) {
                const element = this.normalService.posArr[i];
                if (element.checked) {
                    betPos+=`${i}|`
                }
            }
            betPos = betPos.substring(0,betPos.length-1)
        }
        this.normalService.temp_bet_list.betPos = betPos;

        // 需要修改彩票ID、期号、是否追号1、总金额1、追号是否翻倍1、追号中后是否再追1
        this.normalService.post_bet.bet_list = !this.normalService.post_bet.bet_list?[]:this.normalService.post_bet.bet_list;
        for (const key in this.normalService.temp_bet_list) {
            if (this.normalService.temp_bet_list.hasOwnProperty(key)) {
                this.normalService.temp_bet_list[key] = String(this.normalService.temp_bet_list[key]);
            }
        }
        this.normalService.post_bet.bet_list.push(JSON.parse(JSON.stringify(this.normalService.temp_bet_list)));
        this.normalService.post_bet.amount = !this.normalService.post_bet.amount?0:this.normalService.post_bet.amount;
        this.normalService.post_bet.amount += Number(this.normalService.temp_bet_list.betMoney); //总金额
        this.totalCount = 0;
        this.normalService.post_bet.bet_list.forEach(element=>{
            this.totalCount += Number(element.betCount);
        });
        
        this.openDialog();
    }

    // 将购物车中选好的投注列表下单
    makeBetFromCart() {
        if (!this.normalService.openDatas[this.gameId] || this.normalService.openDatas[this.gameId].timeleft <= 0) {
            return this.snackBar.open("当前期号已关盘，请等待下一期开盘后下单！", "确定", {
                duration: 2000,
              });
        }
        this.normalService.post_bet.bet_next = this.appended?1:0; // 是否追号
        this.normalService.post_bet.bet_more = this.appended?this.moreActualNum:1; //追号期数
        this.normalService.post_bet.bet_win_stop = this.betWinStop?1:0; //追中后是否停止
        this.normalService.post_bet.bet_higher = this.betHigherTime; //追号翻倍倍率
        this.normalService.post_bet.perPeriodNum = this.perPeriodNum;

        this.normalService.post_bet.game_id = Number(this.gameId);
        this.normalService.post_bet.game_period = this.normalService.openDatas[this.gameId].current_period;

        
        this.normalService.doBets(this.normalService.post_bet)
            .subscribe(result=>{
                if (result.code == 200) {
                    this.normalService.User.Coin = result.data.expectBalance;
                    this.snackBar.open(`投注成功，您的当前余额为：${this.normalService.User.Coin}`,"确认", {
                        duration: 2000,
                    });
                    this.normalService.post_bet.bet_list = [];
                    this.normalService.post_bet.amount = 0;
                    this.totalCount = 0;
                    this.appended = false;
                    this.moreActualNum = 5;
                    this.betWinStop = true;
                    this.betHigherTime = 1;
                    this.perPeriodNum = 1;

                } else {
                    this.snackBar.open(result.message, "确定", {duration:2000});
                }
            })
    }

    // 初始化追号倍数列表
    initAppendList() {
        let tempArr = [];
        for (let index = 0; index < this.maxAppendValue; index++) {
            let period = 201902110857;
            let elem = {idx: index, period: period+index, multiple: 1, amount: this.normalService.post_bet.amount, openTime: '2019-02-11 22:40:00', disabled: false, checked: false};
            if (index < 1) {
                elem.disabled = true;
            }
            if (index < 5) {
                elem.checked = true;
            }
            tempArr.push(elem);
        }
        this.dataSource = new MatTableDataSource<any>(tempArr);
    }

    /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.data.forEach(row => this.selection.select(row));
  }

// 选中某一需要追号的行   
  checkRow(row) {
      row.checked = !row.checked;
      if (row.checked) {
          this.dataSource.data.forEach(el=>{
              if (el.period<row.period) {
                  el.checked = true;
              }
          });
      }
      if (!row.checked) {
        this.dataSource.data.forEach(el=>{
            if (el.period>row.period) {
                el.checked = false;
            }
        });
      }
      this.moreActualNum = 0;
      this.dataSource.data.forEach(el=>{
          if (el.checked)
            this.moreActualNum++
      });
  }

// 修改追号期数的select菜单 
  moreNumChanged() {
      this.moreActualNum = Number(this.moreNum);
      this.dataSource.data.forEach( (el,i)=>{
        if (i>=this.moreActualNum) {
            el.checked = false;
        }else {
            el.checked = true;
        }
    });
  }

  // 切换翻倍是否追号，并将翻倍追号倍率设为2
  changeBetHigher(is) {
      if (is==1) {
          this.betHigher = true;
          this.betHigherTime = 2;
      } else {
          this.betHigher = false;
          this.betHigherTime = 1;
      }
      console.log(`${this.betStartTime}*${this.perPeriodNum}*${this.betHigherTime}*`);
  }

// 根据追投列表的行号，获取改行的追号倍率   
  timesWithIndex(index) {
    let times = 1;
    times = this.betStartTime*Math.pow(this.betHigherTime, Math.floor(index/this.perPeriodNum) );
    this.dataSource.data[index].times = times;
    this.dataSource.data[index].sum = times*this.dataSource.data[index].amount;
    return times;
  }

//   计算并返回追号总金额
  sumWithAppend() {
    let sum = 0;
      if (!this.betHigher) {
            sum = this.moreActualNum*this.normalService.post_bet.amount;
      } else {
        this.dataSource.data.forEach(element => {
            if (element.checked)
                sum += element.sum
        });
      }
    return sum;
  }

//   当返点费率发生变化时，修改normalService中的返点率
  percentChange() {
      this.normalService.selectedPercent = this.percent;
  }

  openDialog() {
    if (this.normalService.post_bet.bet_list && this.normalService.post_bet.bet_list.length==0) {
        return this.snackBar.open("您还没有添加投注单", "确定", {
            duration: 2000,
            });
    }
    const dialogRef = this.dialog.open(ConfirmModalDialog, {
      width: '250px',
      data: {title: "确认订单", text: `请确认您的订单，该订单总金额为: ${!this.appended?this.normalService.post_bet.amount:this.normalService.post_bet.amount*this.betHigherTime*this.moreActualNum}元`}
    });

    dialogRef.afterClosed().subscribe(result => {
        if (result && result.result == true) {
            this.makeBetFromCart();
            this.normalService.resetBetList();
            this.normalService.initPlayOptions(this.normalService.selectedSubId);
        }
    });
  }
}