import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from '@angular/router';
import { NestedTreeControl } from '@angular/cdk/tree';
import { MatTreeNestedDataSource } from '@angular/material';
import { NormalService } from '../normal.service';
import { Observable } from 'rxjs';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';

declare const Swiper;

interface LotteryNode {
  name: string;
  href: string;
  groupType?: number;
  children?: LotteryNode[];
}
  

@Component({
    selector: 'business',
    templateUrl: './business.template.html',
    styleUrls: ['./business.style.scss']
})
export class BusinessComponent implements OnInit {
    gameName: string;
    gameId: number;
    lottery: object;
    groupType: number;
    playeds: any[];
    subId: number;

    listLotteries: any[];
    
    isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );

    constructor(
        router: ActivatedRoute,
        private breakpointObserver: BreakpointObserver,
        public normalService: NormalService
    ){
        router.params.subscribe(params=>{
            this.gameId = params.lotId;
            this.gameName = params.lotName;
            this.playeds = normalService.getPlayeds(this.gameId);
            this.groupType = this.playeds[0].groupId;
            if (params.lotType) {
              this.groupType = params.lotType;
            }
            this.getLottery();
        })
        this.getLotteries();
        this.getNotices();
    }
    ngOnInit() {
    }

    hasChild = (_: number, node: LotteryNode) => !!node.children && node.children.length > 0;

    subIdChanged(subId) {
      this.subId = subId;
    }

    getLottery(): void {
      this.normalService.getLotteries(this.gameId)
          .subscribe(lotteries => {
              this.lottery = lotteries.data.Records[0];
          })
    }

    getLotteries(): void {
      if (this.normalService.listLotteries.length>0) {
        this.listLotteries = this.normalService.listLotteries;
      } else {
        this.normalService.getAllLotteries()
          .subscribe(lotteries => {
              this.normalService.listLotteries = lotteries.data.Records;
              this.listLotteries = this.normalService.listLotteries;
          })
      }
    }

    getNotices(): void {
      if (this.normalService.notices.length) {
          return;
      } else {
          this.normalService.getNotices()
              .subscribe(notices => {
              })
      }
    }
}