import { Component, OnInit, Input, OnChanges } from "@angular/core";
import { NormalService } from 'src/app/normal.service';
import { Observable } from 'rxjs';
import { Breakpoints, BreakpointObserver } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';
import { MatDialog } from '@angular/material';
import { SubInfoModal } from '../subInfoModal/subInfoModal.component';
import { RemoveModalDialog } from '../removeModal/removeModal.component';

declare const Swiper;

@Component({
    selector: 'selects',
    styleUrls: ["./selects.style.scss"],
    templateUrl: './selects.template.html',
})
export class SelectsComponent implements OnInit, OnChanges {
    @Input() gameId;
    @Input() subId;
    @Input() sub;
    public posArr: any[] = [];
    public selectedArtIndex = [];

    isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );
    checkedPosNum = 0;
    fangAnNum = 1;
    constructor(
        public normalService: NormalService,
        private breakpointObserver: BreakpointObserver,
        public dialog: MatDialog,
    ){
        
    }
    ngOnInit() {
        
    }

    ngOnChanges() {
        // 手写输入清空
        this.normalService.handinput = '';
        if (this.normalService.subPlayOptions[this.subId] && this.normalService.subPlayOptions[this.subId].betPos) {
            this.posArr = [];
            this.normalService.subPlayOptions[this.subId].betPos.forEach( (element, index) => {
                let checked = false;
                if (this.normalService.subPlayOptions[this.subId].posCheck[index]) {
                    checked = true;
                    this.checkedPosNum++;
                }
                this.posArr.push({pos: element, checked: checked});
            });
        } else {
            this.posArr = [];
        }
        this.calculateFangAn(false);
    }

    calculateFangAn(countBets: boolean) {
        this.checkedPosNum = 0;
        this.fangAnNum = 1;
        this.posArr.forEach(el=>{
            if (el.checked) {
                this.checkedPosNum++;
            }
        })
        // 计算方案数量
        let rightLenth: number;
        if ([108].indexOf(this.subId) != -1) {
            rightLenth = 5;
        }
        if ([106,140,141,142,143,144].indexOf(this.subId) != -1) {
            rightLenth = 4;
        }
        if ([89,95,96,55,61,62,129,130,131,132,133,134,137].indexOf(this.subId) != -1) {
            rightLenth = 3;
        }
        if ([39,47,123,126,124,125,127].indexOf(this.subId) != -1) {
            rightLenth = 2;
        }
        for (let i = this.checkedPosNum; i > this.checkedPosNum-rightLenth; i--) {
            this.fangAnNum *= i;
        }
        for (let ii = rightLenth; ii > 1; ii--) {
            this.fangAnNum /= ii;
        }
        this.normalService.checkedPosNum = this.checkedPosNum;
        this.normalService.fangAnNum = this.fangAnNum;
        this.normalService.posArr = this.posArr;
        if (countBets == true) {
            this.normalService.checkedButtonChanged(this.subId, []);
        }
    }

    addCheckedNum(num, line, index) {
        num.checked = !num.checked;
        this.selectedArtIndex[line] = 0;
        this.normalService.checkedButtonChanged(this.subId, [line, index]);
    }

    isNumber(num) {
        ++num;
        return num;
    }

    selectNum(line, nth, lineNum) {
        let sum = 0;
        switch (nth) {
            case 1:
                line.nums.forEach(element => {
                    element.checked = true;
                });
                break;
            case 2:
            line.nums.forEach(element => {
                sum += Number(element.name);
            });
            line.nums.forEach(element => {
                if (Number(element.name)>(sum/line.nums.length)) {
                    element.checked = true;
                } else {
                    element.checked = false;
                }
            });
                break;
            case 3:
            line.nums.forEach(element => {
                sum += Number(element.name);
            });
            line.nums.forEach(element => {
                if (Number(element.name)<(sum/line.nums.length)) {
                    element.checked = true;
                } else {
                    element.checked = false;
                }
            });
                break;
            case 4: // 单
                line.nums.forEach(element => {
                    if (element.name % 2 == 1) {
                        element.checked = true;
                    } else {
                        element.checked = false;
                    }
                });
                break;
            case 5: // 双
                line.nums.forEach(element => {
                    if (Number(element.name)%2 == 0) {
                        element.checked = true;
                    } else {
                        element.checked = false;
                    }
                });
                break;
            default:
                line.nums.forEach(element => {
                    element.checked = false;
                });
                break;
        }
        this.selectedArtIndex[lineNum] = nth;
        this.normalService.checkedButtonChanged(this.subId, []);
    }

    showInfoAndExample(){
        this.sub.info = JSON.parse(this.sub.Info);
        this.sub.example = JSON.parse(this.sub.Example);
        
        let infoDialog = this.dialog.open(SubInfoModal, {
            width:"400px",
            data: this.sub
        })
    }

    uniq(array){
        var temp = []; //一个新的临时数组
        for(var i = 0; i < array.length; i++){
            if(temp.indexOf(array[i]) == -1){
                temp.push(array[i]);
            }
        }
        return temp;
    }

    trim(str: string) {
        return str.replace(/^\s+|\s+$/g,"");
    }

    // 去掉重复的
    removeRepeat() {
        let str = this.trim(this.normalService.handinput)
        let inputArr = str.split(/,| |\n|\t/);
        let doppelRemovedArr = [];
        let illegalArr = [];
        for (let i = 0; i < inputArr.length; i++) {
            for (let j = i+1; j < inputArr.length; j++) {
                // 去掉完全重复的
                if (inputArr[i] == inputArr[j]) {
                    doppelRemovedArr.push(inputArr[j]);
                    inputArr.splice(j,1);
                    j--;
                }
                // 长度不对为非法
                let rightLenth: number;
                if ([108].indexOf(this.subId) != -1) {
                    rightLenth = 5;
                }
                if ([106,140].indexOf(this.subId) != -1) {
                    rightLenth = 4;
                }
                if ([89,95,96,55,61,62,129,132,134].indexOf(this.subId) != -1) {
                    rightLenth = 3;
                }
                if ([39,47,123,126].indexOf(this.subId) != -1) {
                    rightLenth = 2;
                }
                if (inputArr[j].length!==rightLenth) {
                    illegalArr.push(inputArr[j]);
                    inputArr.splice(j,1);
                    j--;
                }
                // 去掉组选重复的
                if ([95,96,61,62,47,126,132,134].indexOf(this.subId) != -1) { // 组二 组三 组六
                    let tempArrI = inputArr[i].split("").sort().join()
                    let tempArrJ = inputArr[j].split("").sort().join()
                    if (tempArrI == tempArrJ) {
                        doppelRemovedArr.push(inputArr[j]);
                        inputArr.splice(j,1);
                        j--;
                    }
                }
                // 豹子号错误要去掉
                if ([95,96,61,62,47,126,132,134].indexOf(this.subId) != -1) { // 组二 组三 组六
                    if (inputArr[j][0]==inputArr[j][1] && inputArr[j][1]==inputArr[j][2]) {
                        illegalArr.push(inputArr[j]);
                        inputArr.splice(j,1);
                        j--;
                    }
                }
                // 2号组选 组六对子为非法
                if ([47,126,96,62,134].indexOf(this.subId) != -1) { // 组二 组六
                    let tempLength = inputArr[j].split("").length
                    let uniqLength = this.uniq(inputArr[j].split("")).length
                    if (tempLength !== uniqLength) {
                        illegalArr.push(inputArr[j]);
                        inputArr.splice(j,1);
                        j--;
                    }
                }
                // 组三没有对子为非法
                if ([95,61,132].indexOf(this.subId) != -1) { // 组三
                    let tempLength = inputArr[j].split("").length
                    let uniqLength = this.uniq(inputArr[j].split("")).length
                    if (tempLength == uniqLength) {
                        illegalArr.push(inputArr[j]);
                        inputArr.splice(j,1);
                        j--;
                    }
                }
            }
        }
        if (doppelRemovedArr.length>0 || illegalArr.length>0) {
            let data: any = {
                title: "温馨提示",
            }
            if (illegalArr.length) {
                data.text = `去掉错误号码: ${illegalArr.join(",")}`;
                data.title = "成功"
            }
            if (doppelRemovedArr.length) {
                data.text = `去掉重复号：${doppelRemovedArr.join(",")}`;
                data.title = "成功"
            }
            if (!illegalArr.length && !doppelRemovedArr.length) {
                data.text2 = `没有重复或错误号码`
            }
            const dialogRef = this.dialog.open(RemoveModalDialog, {
                width: '450px',
                data: data
            });
        
            dialogRef.afterClosed().subscribe(result => {
                if (result && result.result == true) {
                }
            });
        }
        this.normalService.handinput = inputArr.join(',')
    }
    clearInput() {
        this.normalService.handinput = '';
    }

    DanshiChange() {
        this.normalService.checkedButtonChanged(this.subId, []);
    }
}